import { notateNumber, prefix } from '../../utility/helpers';
import Box from '@mui/material/Box';
import { capitalize, Card, CardContent, Stack, Typography } from '@mui/material';
import Tooltip from '../Tooltip';
import React from 'react';
import ProgressBar from '../common/ProgressBar';
const getRankColor = rank => {
  const colorMap = {
    1: 'success.light',
    2: 'warning.light',
    3: 'secondary.main'
  };
  return colorMap[rank];
};
const globalSkills = ['gaming', 'sailing', 'breeding', 'farming', 'summoning'].toSimpleObject();
const Skills = ({
  skills,
  charName,
  showSkillsRankOneOnly
}) => {
  const hasRankOne = Object.keys(skills || {})?.filter(skillName => skills[skillName]?.rank === 1)?.length > 0;
  if (showSkillsRankOneOnly && !hasRankOne) return null;
  return <Stack data-sentry-element="Stack" data-sentry-component="Skills" data-sentry-source-file="Skills.jsx">
    <Typography variant={'h5'} data-sentry-element="Typography" data-sentry-source-file="Skills.jsx">Skills</Typography>
    <Card data-sentry-element="Card" data-sentry-source-file="Skills.jsx">
      <CardContent data-sentry-element="CardContent" data-sentry-source-file="Skills.jsx">
        <Box sx={{
          display: showSkillsRankOneOnly ? 'flex' : 'grid',
          gridAutoFlow: 'column',
          gap: showSkillsRankOneOnly ? '24px' : 'none',
          ...(showSkillsRankOneOnly ? {
            maxWidth: 500,
            flexWrap: 'wrap'
          } : {}),
          gridTemplateColumns: {
            xs: showSkillsRankOneOnly ? 'fit-content' : `repeat(5, minmax(45px, 100px))`
          },
          gridTemplateRows: showSkillsRankOneOnly ? null : {
            xs: 'repeat(3, minmax(45px, 100px))'
          },
          justifyContent: 'center'
        }} data-sentry-element="Box" data-sentry-source-file="Skills.jsx">

          {Object.keys(skills || {})?.map((skillName, index) => {
            const {
              level,
              rank,
              icon
            } = skills[skillName];
            if (skillName === 'character' || showSkillsRankOneOnly && rank !== 1) return null;
            return <Box key={index}>
              <Tooltip title={<SkillTooltip {...skills?.[skillName]} skillName={skillName} charName={charName} />}>
                <img src={`${prefix}data/${icon}.png`} style={{
                  width: 38,
                  height: 36
                }} alt="" />
              </Tooltip>
              <Typography>Lv {level}</Typography>
              {globalSkills[skillName] ? null : <Tooltip title={'Rank across the account'}>
                <Typography sx={{
                  width: 'fit-content',
                  color: getRankColor(rank),
                  fontWeight: rank === 1 || rank === 2 || rank === 3 ? 'bold' : '400'
                }}>R: {rank}</Typography>
              </Tooltip>}
            </Box>;
          })}
        </Box>
      </CardContent>
    </Card>
  </Stack>;
};
const SkillTooltip = ({
  exp,
  expReq,
  charName,
  skillName,
  level
}) => {
  const percent = exp / expReq * 100;
  return <>
    <Typography variant={'h5'} fontWeight={'bold'} data-sentry-element="Typography" data-sentry-source-file="Skills.jsx">{charName}</Typography>
    <Typography variant={'body1'} data-sentry-element="Typography" data-sentry-source-file="Skills.jsx">{capitalize(skillName)} <Typography variant={'body1'} component={'span'} data-sentry-element="Typography" data-sentry-source-file="Skills.jsx">(Lv. {level})</Typography></Typography>
    <ProgressBar percent={percent} bgColor={'#f3dd4c'} data-sentry-element="ProgressBar" data-sentry-source-file="Skills.jsx" />
    <Typography variant={'body1'} data-sentry-element="Typography" data-sentry-source-file="Skills.jsx">{notateNumber(exp, 'Big')} / {notateNumber(expReq, 'Big')} <Typography variant={'body1'} component={'span'} data-sentry-element="Typography" data-sentry-source-file="Skills.jsx">({Math.round(percent)}%)</Typography>
    </Typography>
  </>;
};
export default Skills;