import { Stack, Typography } from '@mui/material';
import { cleanUnderscore, notateNumber, prefix } from '../../utility/helpers';
import { TitleAndValue } from '../common/styles';
import Tooltip from '../Tooltip';
const Inventory = ({
  inventory,
  inventorySlots
}) => {
  return <Stack sx={{
    width: 250
  }} data-sentry-element="Stack" data-sentry-component="Inventory" data-sentry-source-file="Inventory.jsx">
    <TitleAndValue title={'Capacity'} value={`${inventory?.length ?? 0}/${inventorySlots}`} data-sentry-element="TitleAndValue" data-sentry-source-file="Inventory.jsx" />
    <Stack sx={{
      mt: 1
    }} direction={'row'} flexWrap={'wrap'} data-sentry-element="Stack" data-sentry-source-file="Inventory.jsx">
      {inventory?.map((item, index) => {
        return <Tooltip title={cleanUnderscore(item?.name)} key={item?.rawName + '' + index}>
          <Stack alignItems={'center'} sx={{
            border: '1px solid rgb(255 255 255 / 12%)',
            width: '25%',
            p: 1
          }}>
            <img width={32} height={32} src={`${prefix}data/${item?.rawName}.png`} alt="" />
            <Typography>{notateNumber(item?.amount)}</Typography>
          </Stack>
        </Tooltip>;
      })}
    </Stack>
  </Stack>;
};
export default Inventory;