import { useMemo } from 'react';
import { Card, CardContent, Stack, Typography } from '@mui/material';
import { getCoinsArray, kFormatter, notateNumber, prefix } from 'utility/helpers';
import styled from '@emotion/styled';
import CoinDisplay from '../common/CoinDisplay';
import { calcAnvilExp, getPlayerAnvil } from 'parsers/anvil';
const AnvilDetails = ({
  character,
  account,
  characters
}) => {
  const {
    stats
  } = useMemo(() => getPlayerAnvil(character, characters, account), [character, characters, account]);
  const {
    xpPoints,
    speedPoints,
    capPoints,
    anvilSpeed,
    anvilCapacity,
    anvilCost,
    baseAnvilExp
  } = stats;
  return <Stack data-sentry-element="Stack" data-sentry-component="AnvilDetails" data-sentry-source-file="AnvilDetails.jsx">
      <Typography variant={'h5'} data-sentry-element="Typography" data-sentry-source-file="AnvilDetails.jsx">
        Anvil Details
      </Typography>
      <Stack data-sentry-element="Stack" data-sentry-source-file="AnvilDetails.jsx">
        <Section title={<PointsTitle {...stats} smithingLevel={character?.skillsInfo?.smithing?.level} />} data-sentry-element="Section" data-sentry-source-file="AnvilDetails.jsx">
          <PointsCard title={'Exp'} value={xpPoints} data-sentry-element="PointsCard" data-sentry-source-file="AnvilDetails.jsx" />
          <PointsCard title={'Speed'} value={speedPoints} data-sentry-element="PointsCard" data-sentry-source-file="AnvilDetails.jsx" />
          <PointsCard title={'Capacity'} value={capPoints} data-sentry-element="PointsCard" data-sentry-source-file="AnvilDetails.jsx" />
        </Section>
        <Section title={'Bonus'} data-sentry-element="Section" data-sentry-source-file="AnvilDetails.jsx">
          <PointsCard title={'Exp'} value={`${notateNumber(calcAnvilExp(character, characters, account, baseAnvilExp, xpPoints), 'Big')}%`} data-sentry-element="PointsCard" data-sentry-source-file="AnvilDetails.jsx" />
          <PointsCard title={'Speed'} value={notateNumber(anvilSpeed, 'Big')} data-sentry-element="PointsCard" data-sentry-source-file="AnvilDetails.jsx" />
          <PointsCard title={'Capacity'} value={kFormatter(anvilCapacity)} data-sentry-element="PointsCard" data-sentry-source-file="AnvilDetails.jsx" />
        </Section>
        <Section title={'Material'} data-sentry-element="Section" data-sentry-source-file="AnvilDetails.jsx">
          <PointsCard title={'Item'} value={anvilCost?.rawName ? <MaterialIcon src={`${prefix}data/${anvilCost?.rawName}.png`} alt={''} /> : <></>} data-sentry-element="PointsCard" data-sentry-source-file="AnvilDetails.jsx" />
          <PointsCard title={'Upg. cost'} value={kFormatter(anvilCost?.nextMatUpgrade, 2)} data-sentry-element="PointsCard" data-sentry-source-file="AnvilDetails.jsx" />
          <PointsCard title={'Total Spent'} value={kFormatter(anvilCost?.totalMats)} data-sentry-element="PointsCard" data-sentry-source-file="AnvilDetails.jsx" />
        </Section>
        <Section title={'Money'} data-sentry-element="Section" data-sentry-source-file="AnvilDetails.jsx">
          <PointsCard title={'Upg. cost'} money sx={{
          pb: 2
        }} value={<CoinDisplay title={''} maxCoins={3} money={getCoinsArray(anvilCost?.nextCoinUpgrade)} />} data-sentry-element="PointsCard" data-sentry-source-file="AnvilDetails.jsx" />
          <PointsCard title={'Total Spent'} money sx={{
          pb: 2
        }} value={<CoinDisplay title={''} maxCoins={3} money={getCoinsArray(anvilCost?.totalCoins)} />} data-sentry-element="PointsCard" data-sentry-source-file="AnvilDetails.jsx" />
        </Section>
        {anvilCost?.coinsToMax > 0 ? <PointsCard title={'Coins to max'} money sx={{
        pb: 2,
        my: 2
      }} value={<CoinDisplay title={''} maxCoins={3} money={getCoinsArray(anvilCost?.coinsToMax)} />} /> : null}
      </Stack>
    </Stack>;
};
const MaterialIcon = styled.img`
  width: 30px;
  height: 30px;
`;
const PointsTitle = ({
  availablePoints,
  pointsFromCoins,
  pointsFromMats,
  pointsFromAcme,
  smithingLevel = 0
}) => {
  const color = availablePoints === 0 ? '' : availablePoints > 0 ? 'error.light' : 'secondary';
  return <Stack mb={1} data-sentry-element="Stack" data-sentry-component="PointsTitle" data-sentry-source-file="AnvilDetails.jsx">
      <Typography my={1} variant={'h6'} data-sentry-element="Typography" data-sentry-source-file="AnvilDetails.jsx">
        Points (
        <Typography variant={'h6'} component={'span'} color={color} data-sentry-element="Typography" data-sentry-source-file="AnvilDetails.jsx">
          {pointsFromCoins + pointsFromMats - availablePoints + smithingLevel + pointsFromAcme}
        </Typography>{' '}
        / {pointsFromCoins + pointsFromMats + smithingLevel + pointsFromAcme})
      </Typography>
      <Typography variant="caption" data-sentry-element="Typography" data-sentry-source-file="AnvilDetails.jsx">Points From Mats: {pointsFromMats}</Typography>
      <Typography variant="caption" data-sentry-element="Typography" data-sentry-source-file="AnvilDetails.jsx">Points From Coins: {pointsFromCoins}</Typography>
      <Typography variant="caption" data-sentry-element="Typography" data-sentry-source-file="AnvilDetails.jsx">Points From Smithing: {smithingLevel}</Typography>
      <Typography variant="caption" data-sentry-element="Typography" data-sentry-source-file="AnvilDetails.jsx">Points From acme: {pointsFromAcme}</Typography>
    </Stack>;
};
const PointsCard = ({
  title,
  value,
  money,
  sx
}) => {
  return <Card sx={{
    width: '100%',
    ...sx
  }} data-sentry-element="Card" data-sentry-component="PointsCard" data-sentry-source-file="AnvilDetails.jsx">
      <CardContent sx={{
      '&:last-child': {
        p: '10px'
      }
    }} data-sentry-element="CardContent" data-sentry-source-file="AnvilDetails.jsx">
        <Typography fontWeight={'bold'} data-sentry-element="Typography" data-sentry-source-file="AnvilDetails.jsx">{title}</Typography>
        {money ? value : <Typography>{value}</Typography>}
      </CardContent>
    </Card>;
};
const Section = ({
  title,
  children
}) => {
  return <>
      {typeof title === 'object' ? title : <Typography my={1} variant={'h6'}>
          {title}
        </Typography>}
      <Stack direction={'row'} gap={2} data-sentry-element="Stack" data-sentry-source-file="AnvilDetails.jsx">
        {children}
      </Stack>
    </>;
};
export default AnvilDetails;