import React from 'react';
import styled from '@emotion/styled';
import { Box, Card, CardContent, Stack, Typography } from '@mui/material';
import ItemDisplay from '../common/ItemDisplay';
import { prefix } from 'utility/helpers';
import Tooltip from '../Tooltip';
const Equipment = ({
  equipment,
  tools,
  food,
  account,
  character
}) => {
  return <Stack data-sentry-element="Stack" data-sentry-component="Equipment" data-sentry-source-file="Equipment.jsx">
    <Typography variant={'h5'} data-sentry-element="Typography" data-sentry-source-file="Equipment.jsx">Equipment</Typography>
    <Stack mt={2} direction={'row'} gap={1} flexWrap={'wrap'} justifyContent={'center'} data-sentry-element="Stack" data-sentry-source-file="Equipment.jsx">
      <EquipmentPage items={equipment?.slice(0, 8)} character={character} account={account} data-sentry-element="EquipmentPage" data-sentry-source-file="Equipment.jsx" />
      <EquipmentPage items={equipment?.slice(8)} character={character} account={account} data-sentry-element="EquipmentPage" data-sentry-source-file="Equipment.jsx" />
      <EquipmentPage items={tools} character={character} account={account} data-sentry-element="EquipmentPage" data-sentry-source-file="Equipment.jsx" />
      <EquipmentPage items={food} character={character} account={account} data-sentry-element="EquipmentPage" data-sentry-source-file="Equipment.jsx" />
    </Stack>
  </Stack>;
};
const EquipmentPage = ({
  items,
  character,
  account
}) => {
  return <Box sx={{
    display: 'grid',
    justifyContent: 'center',
    gridTemplateColumns: 'repeat(2, 60px)'
  }} data-sentry-element="Box" data-sentry-component="EquipmentPage" data-sentry-source-file="Equipment.jsx">
    {items?.map((item, itemIndex) => {
      const {
        rawName,
        displayName,
        amount
      } = item;
      return itemIndex < 8 ? <Card sx={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        minHeight: 76
      }} variant={'outlined'} key={`${rawName}-${itemIndex}`}>
          <CardContent sx={{
          '&:last-child': {
            padding: 0
          }
        }}>
            <Stack alignItems={'center'} justifyContent={'center'}>
              <Tooltip title={displayName && displayName !== 'ERROR' ? <ItemDisplay {...item} character={character} account={account} /> : ''}>
                <ItemIcon src={`${prefix}data/${rawName}.png`} alt={rawName} />
              </Tooltip>
              {displayName !== 'ERROR' ? amount : ' '}
            </Stack>
          </CardContent>
        </Card> : null;
    })}
  </Box>;
};
const ItemIcon = styled.img`
  width: 50px;
  height: 50px;
  object-fit: contain;
`;
export default Equipment;