import { Card, CardContent, Stack, Typography } from '@mui/material';
import { cleanUnderscore } from 'utility/helpers';
const PlayerStarSigns = ({
  signs
}) => {
  return <Stack data-sentry-element="Stack" data-sentry-component="PlayerStarSigns" data-sentry-source-file="PlayerStarSigns.jsx">
    <Typography variant={'h5'} data-sentry-element="Typography" data-sentry-source-file="PlayerStarSigns.jsx">Star Signs</Typography>
    <Stack gap={1} data-sentry-element="Stack" data-sentry-source-file="PlayerStarSigns.jsx">
      {signs?.map(({
        starName,
        bonuses
      }, index) => {
        const hasChipBoost = bonuses?.some(({
          chipBoost
        }) => chipBoost > 1);
        return starName !== 'None' ? <Card key={starName + index}>
            <CardContent>
              <Typography fontWeight={'bold'} variant={'body1'}>{cleanUnderscore(starName)}</Typography>
              <Typography color={hasChipBoost ? 'info.light' : ''}>{bonuses?.map(({
                rawName,
                bonus
              }, index) => `${cleanUnderscore(rawName?.replace('{', bonus))}${bonuses.length - 1 === index ? '' : ', '}`)}</Typography>
            </CardContent>
          </Card> : null;
      })}
    </Stack>
  </Stack>;
};
export default PlayerStarSigns;